/* eslint-disable no-sequences */
/* eslint-disable no-unused-expressions */
/* eslint-disable no-unused-vars */
import React from 'react'
import { useState } from 'react'
import TextField from "@material-ui/core/TextField"
import Button from "@material-ui/core/Button"
import { withStyles } from "@material-ui/core/styles";
import Switch from '@material-ui/core/Switch';
import FormGroup from '@material-ui/core/FormGroup';
export default function Text() {

    const [text,setText]=useState('Write here');
    const [mode,setMode]=useState('Light');
    const [mystyle,setstyle]=useState({
        backgroundColor:"black",
        color:"white"
    }
    )

    const upper=()=>
    {
        const newText=text.toUpperCase();
        console.log(newText);
setText(newText);
    }
   
    const clear=()=>
    {
       setText("");
    }
    const lower=()=>
    {
        const newText=text.toLowerCase();
        console.log(newText);
setText(newText);
    }
    const update=(event)=>
    {
// eslint-disable-next-line no-restricted-globals
const x1=event.target.value;
setText(x1);


    }
    const copyToClipboard=()=>
    {

navigator.clipboard.writeText(text);


    }
    const chng=()=>
    {
        if(mode==="Dark")
        {
            setMode("Light");
           
                    // eslint-disable-next-line no-unused-expressions
                    document.body.style.backgroundColor="white";
                    document.body.style.color="black";
                
            
        }
        else
        {
            setMode("Dark");
            
            document.body.style.backgroundColor="black",
                document.body.style.color="white";
            
            

    }
    }
    // setText('Hello');
    return (
<>
        <div id="centre">

   <Switch defaultChecked onChange={chng}/>
<TextField id="text1" fullWidth multiline value={text} size="small" variant="standard"  onChange={update}/>
<Button className="btn" onClick={upper} style={mystyle}>UpperCase</Button>
<Button className="btn" onClick={lower} style={mystyle}>LowerCase</Button>
<Button className="btn" onClick={clear} style={mystyle}>Clear</Button>
<Button className="btn" onClick={copyToClipboard} style={mystyle}>Copy</Button>
        </div>
       
        <div className="extra" >You have typed {text.length} characters </div>
        
        </>
    )

    }